@font-face {
  font-family: headingFont;
  src: url();
}
body{
  background-color: rgb(20,20,20);
  margin: 0px;
}

body::-webkit-scrollbar {
  display: none;
}

.swapY{
  transform: scaleY(-1);
  position: relative;
  z-index: 2;
}

/* MENU */
.nav{
  height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.intro {
  margin-top: 1rem;
  margin-left: 3rem;
  display: flex;
  flex-direction: row;
  white-space: nowrap;
  overflow: hidden;
  height: clamp(3rem, 8vh, 6rem);
  width: clamp(10rem, 40vw, 40rem);
  border-radius: 0.5em;
  background-color: rgb(40, 40, 40);
  color: rgb(240, 240, 240);
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Lucida Sans Typewriter, Lucida Console, monaco, Bitstream Vera Sans Mono, monospace;
}

.name{
  color: #7848c4;
}

.menu-links{
  margin-top: 1rem;
  margin-left: auto;
  margin-right: 2rem;
  display: flex;
  flex-direction: row;
  height: clamp(3rem, 8vh, 6rem);
  width: clamp(5rem, 10vw, 10rem);
  border-radius: 0.5em;
  background-color: rgb(40, 40, 40);
  justify-content: center;
  align-items: center;
  
}

.github-icon{
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
  background-color: rgb(220, 220, 220);
  margin-left: 0.5em;
  margin-right: 1em;
}

.linkedin-icon {
  height: 1.75rem;
  width: 1.75rem;
  padding: 0.25rem;
  border-radius: 10%;
  background-color: rgb(40, 40, 40);
  margin-left: 1em;
  margin-right: 0.5em;
}

.menu{
  height: 90vh;
  display: flex;
  flex-direction: row;
  /* justify-content: center; */
  align-items: center;
}

html{
  scroll-behavior: smooth;
}

.row{
  display: flex;
  flex-direction: row;
  align-items: center;
  width: min-content;
  transform: scaleX(-1);
  height : clamp(3rem, 6vw,8rem);
  margin-top: 5%;
  margin-bottom: 2.5%;
}

/* Contents Bar */
.contents-bar{
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
}

#bar{
  width: 1em;
  height: 80vh;
  background-color: #3a2963;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.bar-wrapper{
  display: flex;
  min-width: 18em;
  align-items: center;
  justify-content: center;

}

.bar-peg{
  width: 9em;
  height: 0.75em;
  background-color: #916FFF;
  transition: width 0.4s;
  align-items: center;
  justify-content: center;
  /* margin-right: 3em; */
}

.bar-projects {
  width: 12em;
  height: 0.75em;
  background-color: #4b27a1;
  transition: width 0.3s;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: clamp(9rem, 22vw, 24rem);
}

.bar-contact {
  width: 12em;
  height: 0.75em;
  background-color: #4b27a1;
  transition: width 0.5s;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: clamp(6rem, 14vw, 16rem);
}


/* Table of Contents */

.menu-items-wrapper{
  width: 70vmax;
  display: flex;
  justify-content: center;
}

#menu-items{
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
  height: fit-content;
  /* background-color: rgb(30,30,30); */
  border-radius: 0.5em;
  /* height: 23vmax;
  width: 50vmax; */
  width: 100%;
  /* justify-content: center; */
  /* align-items: center; */

}

.menu-item{

  color: rgb(220,220,220);
  font-size: 8vw;
  font-family: "Franklin Gothic Medium",
    "Franklin Gothic",
    "ITC Franklin Gothic",
    Arial,
    sans-serif;
  text-decoration: none;
  display: block;
  transform: scaleX(-1);
  position: relative;
  transition: font-size 0.4s, color 0.2s;
  z-index: 2;
}

.menu-item-2 {

  color: rgb(240, 240, 240);
  font-size: 8vw;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-decoration: none;
  display: block;
  transform: scaleX(-1);
  position: relative;
  transition: font-size 0.4s, color 0.2s;

}

