/* Menu Effects  */
#menu-items:hover >.row > .menu-item{
  color: rgb(70,70,70);
}

#menu-items:hover>.row> .bar-wrapper >.bar-peg {
  width: 6em;
}

#menu-items:hover > .row> .menu-item:hover ~ .bar-wrapper >.bar-peg{
  width: 8em;
}

#menu-items:hover>.row>.menu-item:hover {
  color: rgb(240, 240, 240);  
  font-size: 8.5vw;
}

/* For contact and projects */
#menu-items:hover>.swapY>.row> .swapY>.menu-item-2 {
  color: rgb(70, 70, 70);
}

#menu-items:hover>.swapY>.row>.bar-wrapper>.bar-peg {
  width: 6em;
}

#menu-items:hover>.swapY>.row>.swapY:hover~.bar-wrapper>.bar-peg {
  width: 9em;
}

#menu-items:hover>.swapY>.row>.swapY>.menu-item-2:hover {
  color: rgb(240, 240, 240);
  font-size: 8.5vw;
}

/* Logo Transitions on Menu */



#contact {
  height: 100vh;
}

.contact-blurb{
  margin-left: 3rem;
  display: flex;
  flex-direction: row;
  white-space: nowrap;
  overflow: hidden;
  height: clamp(3rem, 8vh, 6rem);
  width: clamp(10rem, 40vw, 40rem);
  border-radius: 0.5em;
  background-color: rgb(20, 20, 20);
  color: rgb(240, 240, 240);
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Lucida Sans Typewriter, Lucida Console, monaco, Bitstream Vera Sans Mono, monospace;
}

.contact-message{
  border: none;
  margin-left: 3rem;
  margin-top: 1rem; 
  display: flex;
  flex-direction: row;
  white-space: nowrap;
  overflow: hidden;
  height: 40vh;
  width: 90vw;
  border-radius: 0.5em;
  background-color: rgb(20, 20, 20);
  color: rgb(240, 240, 240);
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Lucida Sans Typewriter, Lucida Console, monaco, Bitstream Vera Sans Mono, monospace;
  text-align: left;
  vertical-align: top;
  padding:2rem;
  overflow: hidden;

}
textarea {
  outline: none;
  vertical-align: top;
  resize: none;
  
}
.contact-button-container{
  height: 15vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-button{
  border: none;
  margin-left: clamp(0.5rem, 1rem, 3rem);
  margin-right: clamp(0.5rem, 1rem, 3rem);
  height: clamp(3rem, 4vh, 6rem);
  width: clamp(10rem, 30vw, 20rem);
  text-decoration: none;
  color: rgb(220, 220, 220);
  /* border: solid #916FFF; */
  background-color: #7848c4;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 1.5em;
  border-radius: 0.3em;
  transition: background-color 0.2s;

}

.contact-button:hover{
  background-color: #916FFF;
  cursor: pointer;
}